@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
}
*,
::before,
::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #333333;
  background-color: #ffffff;
}

main {
  flex-grow: 1;
}

.reset-link {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
  width: 100%;
  object-fit: cover;
}

.font-family {
  font-family: "Montserrat", "Arial", sans-serif;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.container-main {
  margin-left: 20px;
  margin-right: 20px;
}
@media (min-width: 1440px) {
  .container-main {
    margin-left: auto;
    margin-right: auto;
    width: 1360px;
  }
}

.main-header {
  z-index: 2;
}
@media (max-width: 767px) {
  .main-header {
    margin-right: 0;
  }
}

.main-header__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 80px 1fr;
  grid-template-areas: "logo user" "menu menu";
}
@media (min-width: 768px) {
  .main-header__wrapper {
    grid-template-columns: 124px 1fr 160px;
    grid-template-areas: unset;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    grid-template-columns: 350px 1fr 300px;
  }
}

.main-header__logo-link-wrapper {
  display: flex;
  align-items: center;
}

.main-header__logo-link {
  display: flex;
  align-items: center;
  padding: 12px 0;
  line-height: 0;
  grid-area: logo;
}
@media (min-width: 768px) {
  .main-header__logo-link {
    grid-area: unset;
    min-height: 80px;
    padding: 0;
  }
}
.main-header__logo-link:hover, .main-header__logo-link:focus-visible {
  opacity: 0.6;
  outline: none;
}
.main-header__logo-link:active {
  background-color: #f0ebff;
}

@media (min-width: 768px) {
  .main-header__logo {
    width: 82px;
    height: 36px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo {
    height: 36px;
    min-width: 170px;
  }
}

@media (max-width: 1439px) {
  .main-header__logo-link-description {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
  }
}
@media (min-width: 768px) {
  .main-header__logo-link-description {
    font-size: 12px;
    line-height: 16px;
    margin-left: 20px;
  }
}

.main-header__navigation {
  grid-area: menu;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .main-header__navigation {
    position: relative;
    top: 0;
    left: -20px;
    width: 100vw;
    z-index: 1;
  }
  .main-header__navigation--with-js {
    position: absolute;
  }
  .main-header__navigation--with-js ~ .main-header__user-buttons {
    margin-right: 0;
  }
}
@media (min-width: 768px) {
  .main-header__navigation {
    grid-area: unset;
  }
}
@media (max-width: 767px) {
  .main-header__navigation--hidden {
    display: none;
  }
}

.main-header__navigation-list {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.02em;
}
@media (min-width: 768px) {
  .main-header__navigation-list {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .main-header__navigation-list {
    column-gap: 20px;
  }
}

.main-header__navigation-item {
  position: relative;
}
@media (max-width: 767px) {
  .main-header__navigation-item {
    border-top: 1px solid #f0ebff;
  }
  .main-header__navigation-item:last-child {
    border-bottom: 1px solid #f0ebff;
  }
}
@media (min-width: 768px) {
  .main-header__navigation-item--current {
    color: #7859cf;
  }
  .main-header__navigation-item--current::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #7859cf;
  }
}

.main-header__navigation-link {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 58px;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .main-header__navigation-link {
    min-height: 80px;
  }
}
.main-header__navigation-link:hover, .main-header__navigation-link:focus-visible {
  color: #7859cf;
  outline: none;
}
.main-header__navigation-link:active {
  background-color: #f0ebff;
}

.main-header__user-buttons {
  grid-area: user;
  display: flex;
  justify-content: flex-end;
  align-items: start;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: 20px;
}
@media (min-width: 768px) {
  .main-header__user-buttons {
    grid-area: unset;
    margin-right: 0;
  }
}

.main-header__user-button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 60px;
  min-width: 60px;
  padding: 0 20px;
  border: none;
  background-color: inherit;
}
.main-header__user-button:hover, .main-header__user-button:focus-visible {
  color: #7859cf;
  outline: none;
}
.main-header__user-button:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .main-header__user-button {
    min-height: 80px;
  }
}
@media (min-width: 1440px) {
  .main-header__user-button {
    margin-left: 24px;
  }
  .main-header__user-button:hover > .main-header__user-icon, .main-header__user-button:focus-visible > .main-header__user-icon {
    fill: #7859cf;
  }
}

.main-header__user-icon {
  fill: #333333;
}

@media (max-width: 1439px) {
  .main-header__user-button-description {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
  }
}
@media (min-width: 1440px) {
  .main-header__user-button-description {
    margin-left: 8px;
  }
}

.main-header__menu-toggler {
  display: block;
  position: relative;
}
.main-header__menu-toggler--hidden {
  display: none;
}
.main-header__menu-toggler--closed::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 12px;
  background-image: linear-gradient(#333333, 2px, #ffffff 2px, 5px, #333333 5px, 7px, #ffffff 7px, 10px, #333333 10px);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.main-header__menu-toggler--opened::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 2px;
  background-color: #333333;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}
.main-header__menu-toggler--opened::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 2px;
  background-color: #333333;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}
@media (min-width: 768px) {
  .main-header__menu-toggler {
    display: none;
  }
}

@media (min-width: 1440px) {
  .main-footer__desktop-grid {
    margin-left: 20px;
    margin-right: 20px;
    display: flex;
    column-gap: 30px;
  }
}
@media (min-width: 1440px) and (min-width: 1440px) {
  .main-footer__desktop-grid {
    margin-left: auto;
    margin-right: auto;
    width: 1360px;
  }
}

.main-footer__first-line > .container-main {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1440px) {
  .main-footer__first-line > .container-main {
    flex-direction: column;
    align-items: start;
    width: 318px;
  }
}

.main-footer__first-line {
  padding-top: 20px;
  padding-bottom: 20px;
}
@media (max-width: 767px) {
  .main-footer__first-line {
    border-bottom: #f0ebff solid 1px;
  }
}
@media (min-width: 768px) {
  .main-footer__first-line {
    padding-top: 40px;
    padding-bottom: 28px;
  }
}
@media (min-width: 1440px) {
  .main-footer__first-line {
    width: 318px;
  }
}

.main-footer__logo-link-description {
  font-size: 14px;
  line-height: 20px;
  margin-top: 5px;
}
@media (max-width: 1439px) {
  .main-footer__logo-link-description {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
  }
}

.main-footer__logo-link:hover, .main-footer__logo-link:focus-visible {
  opacity: 0.6;
  outline: none;
}
.main-footer__logo-link:active {
  background-color: #f0ebff;
}

.main-footer__social-links {
  display: flex;
  align-items: center;
  column-gap: 20px;
}
@media (min-width: 1440px) {
  .main-footer__social-links {
    margin-top: 50px;
  }
}

.main-footer__social-link {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-footer__social-link:focus-visible {
  outline: none;
}
.main-footer__social-link:hover > .main-footer__social-svg, .main-footer__social-link:focus-visible > .main-footer__social-svg {
  fill: #7859cf;
}
.main-footer__social-link:active > .main-footer__social-svg {
  fill: #593cac;
}

.main-footer__social-svg {
  fill: #bdbdbd;
}

@media (min-width: 768px) {
  .main-footer__about {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin-left: 20px;
    margin-right: 20px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .main-footer__about {
    margin-left: auto;
    margin-right: auto;
    width: 1360px;
  }
}
@media (min-width: 1440px) {
  .main-footer__about {
    padding-top: 48px;
  }
}

.main-footer__section-about {
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .main-footer__section-about {
    padding: 20px;
    padding-bottom: 28px;
    border-bottom: #f0ebff solid 1px;
  }
}

.main-footer__about-header {
  margin: 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #7859cf;
}

.main-footer__about-link {
  margin-top: 20px;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
.main-footer__about-link--address {
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  text-transform: none;
  padding-left: 18px;
}
.main-footer__about-link:hover, .main-footer__about-link:focus-visible {
  color: #7859cf;
  outline: none;
}
.main-footer__about-link:active {
  color: #593cac;
}

.main-footer__contacts {
  padding-bottom: 20px;
}

.main-footer__phone-icon {
  position: absolute;
  top: 0;
  left: 0;
  fill: #7859cf;
}

.main-footer__created-by {
  padding: 20px;
  padding-bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #bdbdbd;
}
@media (min-width: 768px) {
  .main-footer__created-by {
    padding-top: 36px;
    padding-bottom: 36px;
  }
}
@media (min-width: 1440px) {
  .main-footer__created-by {
    padding-top: 16px;
  }
}

.main-footer__created-link {
  display: flex;
  align-items: center;
}
.main-footer__created-link:hover, .main-footer__created-link:focus-visible {
  color: #7859cf;
  outline: none;
}
.main-footer__created-link:hover .main-footer__academy-logo, .main-footer__created-link:focus-visible .main-footer__academy-logo {
  fill: #7859cf;
}
.main-footer__created-link:active {
  fill: #593cac;
}

.main-footer__academy-logo {
  fill: #bdbdbd;
