/* The container must be positioned relative: */
/* https: //www.w3schools.com/howto/howto_custom_select.asp */
/* The container must be positioned relative: */
.custom-select {
  position: relative;
  min-width: 165px;
}

.custom-select select {
  display: none;
  /*hide original SELECT element: */
}

.select-selected {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #7859cf;
  background-color: transparent;
  padding: 4px 30px 4px 10px;
}
.select-selected:hover {
  background-color: #f0ebff;
  border-radius: 4px;
}

/* Style the arrow inside the select element: */
.select-selected::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 6px;
  top: 44%;
  right: 10px;
  background-image: url("../img/icons/bird-of-selector.svg");
}

/* Point the arrow upwards when the select box is open (active): */
.select-selected.select-arrow-active::after {
  transform: scale(1, -1);
}

/* style the items (options), including the selected item: */
.select-items div,
.select-selected {
  border: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #7859cf;
  background-color: transparent;
}

.select-items div:nth-child(1) {
  border-radius: 4px 4px 0 0;
}

.select-items div:last-child {
  border-radius: 0 0 4px 4px;
}

.select-items div:not(:last-child) {
  border-bottom: 1px solid #f0ebff;
}

.select-items div {
  padding: 8px 20px;
  font-weight: 400;
}

/* Style items (options): */
.select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  right: 0;
  z-index: 1;
  min-width: 190px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #333333;
  border-radius: 4px;
}

/* Hide the items when the select box is closed: */
.select-hide {
  display: none;
}

.select-items div:hover,
.same-as-selected {
  background-color: #f0ebff;
}

.same-as-selected {
  background-color: #7859cf;
  color: #ffffff;
}

.custom-select__without-js {
  display: block;
  border: none;
  background-color: transparent;
  appearance: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #7859cf;
  font-family: "Montserrat", "Arial", sans-serif;
  padding: 4px 30px 4px 10px;
  width: 165px;
  position: relative;
  background-image: url("../img/icons/bird-of-selector.svg") no-repeat right 10px center;
  cursor: pointer;
}
.custom-select__without-js:hover, .custom-select__without-js:focus {
  background-color: #f0ebff;
  border-radius: 4px;
  outline: none;
}